import VueDragResize from '~/node_modules/vue-drag-resize/src/components/vue-drag-resize.vue'
import { createNamespacedHelpers } from 'vuex'
export default {
  name: 'DragResize',
  components: { VueDragResize },
  props: {
    coordinates: {
      type: Object,
      required: true
    },
    width: {
      type: Number,
      default: 45
    },
    height: {
      type: Number,
      default: 42
    },
  },
  methods: {
    drag(newRect, e) {
      this.$emit('update:coordinates', {
        top: newRect.top,
        left: newRect.left
      })
    },
  }
}
